.Notifications {
    border: 2px dashed #e4384f;
    padding-left: 5px;
    padding-top: 0;
    padding-bottom: 10px;
}

.Notifications p, .Notifications ul {
    font-size: 1.2em;
    font-family: 'Times New Roman', Times, serif;
}

[data-priority="default"] {
    color: rgb(12, 12, 122);
}

[data-priority="urgent"] {
    color: rgb(233, 16, 16);
}